@import url('https://fonts.googleapis.com/css?family=Roboto:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&display=swap&subset=cyrillic');
*
{
    --color60: rgb(219, 246, 237);
    --color30: #ffffff;
    --color10: rgb(20, 193, 240);
}
body
{
    box-sizing: border-box;
    
    background-color: var(--color30);
    font-size: 24px;
}
.App
{
    background-color: var(--color30);
}
*::-webkit-scrollbar
{
    width: 0px;

    
}
*::-webkit-scrollbar-track 
{
    background: orange;        /* цвет дорожки */
}
*::-webkit-scrollbar-thumb 
{
    background-color: blue;    /* цвет плашки */
    border-radius: 20px;       /* закругления плашки */
    border: 3px solid orange;  /* padding вокруг плашки */
}
  
.chat
{
    height: 100%;
    display: flex;
    flex-direction: column;
    
    width: 900px;
    margin: 0 auto;
    font-family:  "Roboto", sans-serif;
    position: relative;
}
.chat .view
{
    display: flex;
    flex-direction: column-reverse;
    flex-grow: 19;
    background-color: var(--color60);
    margin: 0px;
    border: 2px solid var(--color10);
    border-radius: 50px;
    margin-bottom: 12%;
    overflow-y: auto;
    padding: 20px 0;
}
.chat .send 
{
    height: 50px;
    display: flex;
    flex-direction: row;
    margin: 15px 0px 15px 50px;
    justify-content: space-evenly;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;

}
.chat .send .msg-field
{
    display: block;
    border-radius: 30px;
    font-size: 24px;
    width: 80%;
    background-color: var(--color60);
    border: none;
    outline: none;
    padding: 0 20px;
    font-family: inherit;
    font-weight: 400;
}
.chat .send .msg-submit
{
    display: block;
    width: 100px;
    background: url('../../public/images/icon/send-button.png');

    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50%;
    margin: 0 1px;
    border: none;
    cursor: pointer;
}
.chat .message
{
    flex-direction: column;
    background-color: var(--color30);
    height: max-content;
    border-radius: 20px;
    margin: 15px 50px;
    padding: 20px 30px;
    display: flex;
    box-shadow: 2px 3px 7px var(--color10);
}
.chat .author
{
    flex-grow: 10;
    color: var(--color10);
    font-size: 26px;
    width: fit-content;
    height: fit-content;
    left: 0;
    font-weight: 900;

}
.chat .data
{
    font-size: 24px;

    height: fit-content;
    
    width: 95%;
    word-wrap: break-word;
 
}
.chat #msg-file
{
    z-index: -100;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
}
.chat label[for = "msg-file"]
{
    display: block;
    width: 60px;
    height: 50px;
    margin-right: 10px;
    position: relative;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    background-image: url(/public/images/icon/upload.png);
    cursor: pointer;
}
@media screen and (max-width: 1000px) {
    
    body
    {
        margin: 0;
    }
    .chat
    {
        display: flex;
        width: 100%;
        height: 100%;
    }
    .chat .view
    {
        margin: 0;
        padding: 0;
        height: 100%;
        border: none;
        border-radius: 0;
        
        flex-grow: 0;
    }
    .chat .send
    {
        position: relative;
        height: 80px;
        margin: 0;
        display: flex;
    }
    .chat .message
    {
        margin: 10px;
        box-shadow: 2px 3px 7px var(--color10);
        border-radius: 17px;
    }
    .chat .message .data
    {
        font-size: 25px;
    }
    .chat .message .author 
    {
        font-size: 29px;
    }
    .send .msg-field
    {
        height: 50px;
        margin: 15px 0 15px 10px;
    }
    .send .msg-submit[type = "submit"]
    {
        min-width: 50px;
        height: 50px;
        margin: 15px 0;
    }
    .send label[for = "msg-file"]
    {
        min-width: 50px;
        height: 50px;
        margin: 15px 0;
    }



}
